import React, { useState } from "react";
import AddIcon from '@mui/icons-material/Add';
import Zoom from '@mui/material/Zoom';
import Fab from '@mui/material/Fab';
import Collapse from '@mui/material/Collapse';


function CreateArea(props) {

    const [input,setInput]=useState({
      title : "",
      content : ""
    });
   const [checked , setChecked] = useState(false)
    
    function handleChange(event){
       const {name,value} =  event.target;
        setInput(prevInput =>{
            return {
                ...prevInput,
                [name] : value,
            }
        })
    }

    function handleSubmit(event){
      console.log("clicked");
      props.onAdd(input);
      setInput({
        title : "",
        content : ""
      });
     event.preventDefault();
      
    }
    function onCollapse(){
      setChecked(true);
    }

  return (
    <div >
      <form  className="create-note">
    
   
        <input value ={input.title} onClick = {onCollapse} onChange={handleChange} name="title" placeholder="Title" />
       
        <Collapse in = {checked} >

        <textarea value ={input.content} onChange={handleChange} name="content" placeholder="Take a note..." rows="3" />
       <Zoom in ={true}> 
        <Fab onClick = {handleSubmit}><AddIcon /></Fab>
       </Zoom>
       </Collapse>
       
        
      </form>
    </div>
  );
}

export default CreateArea;
